
import {mavonEditor} from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name:'create',
components: {
XTextarea,
Group,
XButton,
mavonEditor

},
data:function(){
return{
title:'',
content:'',

toolbars: {
bold: true, // /
italic: true, // 
header: true, // 
underline: true, // 
strikethrough: true, // 
mark: true, // 
superscript: true, // 
subscript: true, // 
quote: true, // 


link: true, // 
imagelink: true, // 
code: true, // code
table: true, // 


htmlcode: true, // html
help: true, // 
/* 1.3.5 */
undo: true, // 
redo: true, // 
trash: true, // 
save: true, // eventssave
/* 1.4.2 */
navigation: true, // 
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */
subfield: true, // 
preview: true, // 
}
}
},
methods:{

addtopic:function(){

console.log('title='+this.title);
console.log('content='+this.content);



this.$router.replace('/topiclist/dev')


},

uploadImage:function(pos,file){






var formdata=new FormData();
formdata.append('smfile',file)
this.$http.post('https://sm.ms/api/v2/upload',formdata,{


header:{
'Content-Type': 'multipart/form-data'
}
}).then(function(response){

console.log(response);
})
}
}
}



